<template>
  <el-form label-position="left" label-width="80px">
    <div class="form-item">
      <el-input v-model="item.filename"
                placeholder="请输入文件名称"
                @change="$emit('inputChange',index)"></el-input>
      {{note}}
    </div>
    <div class="form-item">
      <el-input type="textarea" v-model="item.content"
                placeholder="请输入文件内容"
                @change="$emit('contentChange',index)"></el-input>
    </div>
  </el-form>
</template>
<script>
  import http from "axios"

  export default {
    name: "TabContent",
    props:["files","item","index","id"],
    data(){
      return {
        note:""
      }
    },
    watch:{
      'item.filename'(newVal){
          var arr=this.files.filter(val=>val.deleted !== undefined);
          if((arr.filter(val => val.filename === newVal)).length>1){
              this.note ="文件名重复，请重新填写";
          } else {
              this.note="";
          }
      }
    }
  }
</script>
<style scoped>
  .form-item{
    padding-bottom: 20px;
  }
</style>



