<template>
  <div>
    <Example :list="list" v-for="(list,index) in lists" :key="index"></Example>
  </div>

</template>
<script>
  import http from 'axios'
  import Example from '@/components/Example'

  export default {
    name: "Test",
    components:{Example},
    data(){
      return {
        size:"12",
        description:"",
        lists:[
          {
            "name":"js",
            "id":0,
            "content":"JavaScript 是世界上最流行的脚本语言。JavaScript 是属于 web 的语言，它适用于 PC、笔记本电脑、平板电脑和移动电话。JavaScript 被设计为向 HTML 页面增加交互性。"
          },
          {
            "name":"css",
            "id":1,
            "content":"HTML 指超文本标签语言。HTML 是通向 WEB 技术世界的钥匙。在 W3School 的 HTML 教程中，您将学习如何使用 HTML 来创建站点。"
          },
          {
            "name":"HTML 教程",
            "id":2,
            "content":"在本教程中，你将学习如何使用 HTML 来创建站点。HTML 很容易学习！你会喜欢它的！"
          }
        ]
      }
    },
    methods:{
      addGist(){
          http.post("https://api.github.com/gists",{
            description:this.description,
            "files": {
              "b": {
                "content": "b"
              }
            }
          },{
            headers: {
              "Authorization": "token " + localStorage.getItem("token")
            }
          }).then((r)=>{
              console.log(r.data);
          })
            .catch();
      },
      testBtn(index){
        console.log(index);
      },
      sizeFont(){
          this.size ++;
          console.log(this.size);
      }
    }
  }
</script>
<style scoped></style>



