<template>
  <div id="app">
    <UserBar></UserBar>
    <div :class="{ githubBtn:true,hide:hideLogin}">
      <a href="https://github.com/login/oauth/authorize?client_id=d327e486430e4b05f07b&scope=gist,user">
        使用github帐号登录
      </a>
    </div>
    <p :class="{hide:hideLoad}">页面正在加载中</p>
    <router-view>
    </router-view>
  </div>
</template>

<script>
  import http from "axios"
  import url from "query-string"
  import client from "./js/github-client"
  import UserBar from '@/components/List/UserBar'
  import bus from "./bus";

  export default {
    name: 'App',
    components:{UserBar},
    data(){
      return{
        token:"",
        queryObj:{}
      }
    },
    computed:{
      hideLogin(){
        return this.token !== "" || typeof this.queryObj.code !=="undefined" ;
      },
      hideLoad(){
        return this.token !== "" || typeof this.queryObj.code==="undefined"
      }
    },
    mounted(){
      bus.$on("removeToken",  () => {
        localStorage.removeItem("token");
        this.token = "";
        this.queryObj = {}
      });

      if(localStorage.getItem("token") === null){
        this.token = "";
      } else if(localStorage.getItem("token")!==""){
        bus.$emit("gotAccessToken");
        this.token = localStorage.getItem("token");
        this.$router.push("gists");
      }
      this.queryObj = url.parse(location.search);
      if(typeof this.queryObj.code !== 'undefined'){
        http.get("https://joyfunphp.herokuapp.com/github/access/", {
          params: {
            client_id: "d327e486430e4b05f07b",
            code: this.queryObj.code
          }
        }).then((r) => {
          if (r.data.access_token) {
            localStorage.setItem("token", r.data.access_token);
            this.token = r.data.access_token;
            this.$router.push("gists");
            bus.$emit("gotAccessToken");
          } else {
            alert("code过期");
          }
        }).catch();
      } else {

      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }
  .githubBtn{
    text-align: center;
  }
  .githubBtn a{
    display: inline-block;
    margin-top: 100px;
    border-radius:50px;
    padding: 10px 20px;
    background-color: #409eff;
    color:#fff;
  }
  .githubBtn a:hover{
    background-color: #3a91eb;
  }
  /*公共样式*/
  ul{
    list-style-type: none;
  }
  a{
    text-decoration: none;
    color:#333;
  }
  .content {
    width:500px;
  }
  .comWidth{
    margin: 0 auto;
    width:1200px;
  }
  .item{
    margin-bottom:10px;
  }
  .note{
    font-size:12px;
    color:#f40;
  }
  .fl{
    float: left;
  }
  .fr{
    float: right;
  }
  body{
    font-size:12px;
    padding: 0;
    margin: 0;
  }
  .clear:after{
    content:"";
    clear:both;
    display: block;
    height: 0;
    overflow: hidden;
  }
  .hide{
    display: none;
  }
  a{
    cursor: pointer;
  }
</style>



