<template>
  <li @click="$emit('changeCurrent',index)">
    <router-link :to="{path:'/gists/gist', query:{id:item.id}}">
      <span class="item-desc">{{item.description}} </span>
      <span class="fr item-number">文件数量：<b>{{Object.values(item.files).length}}</b></span>
    </router-link>
  </li>
</template>

<script>
  import bus from "./../../bus.js"
  export default {
    name: "ListItem",
    props:["item","index"],
    mounted(){
        bus.$on("defaultTab",(id) => {
            console.log(id);
        });
    }
  }
</script>

<style scoped>

</style>



