<template>
  <el-button size="mini" @click="addFile">添加文件</el-button>
</template>
<script>
  export default {
    name: "addFilename",
    props: ["files"],
    data(){
      return {
        filename: "",
        content: ""
      }
    },
    methods: {
      addFile(){
          this.files.push({
            filename:this.filename,
            content:this.content
          });
      }
    }
  }
</script>
<style scoped></style>



