<template>
  <div>
    <h3 class="title">详情描述</h3>
    <el-input type="textarea" v-model="description"></el-input>
    <h3 class="title">文件列表</h3>
    <div class="tab-tit clear">
      <TabTit class="fl"
              :item="item"
              :index="index"
              :selected="selected"
              v-for="(item,index) in files"
              :key="item.filename"
              @disabledBtn = "onDisableBtn"
              @changeTab="onChangeTab(index)">
      </TabTit>
    </div>
    <TabContent
      :files="files"
      :item="item"
      :index="index"
      :id="id"
      v-for="(item,index) in files"
      :key="index"
      v-show="selected === index"
      @inputChange="onInputChange"
      @contentChange="onContentChange">
    </TabContent>
    <div class="btn">
      <el-button type="primary" size="medium" :disabled="disabledSave"
                 @click="onUpdateItem">更新内容</el-button>
      <el-button size="medium" @click="addFile">添加文件</el-button>
    </div>
  </div>
</template>

<script>
  import http from "axios"
  import bus from './../../bus.js'
  import TabTit from '@/components/List/TabTit'
  import TabContent from '@/components/List/TabContent'

  export default {
    name: "Gist",
    components: {TabTit, TabContent},
    data() {
      return {
        id: this.$route.query.id,
        hideTab:true,
        files: [],
        activeName: "",
        selected: 0,
        gistChanged: false,
        disabledSave: false,
        data: {},
        description: "",
        newFilename: "filename",
        newContent: "",
        deleteItem: []
      }
    },
    beforeRouteUpdate(to, from, next) {
      console.log(to);
      if (this.gistChanged) {
        next(false);
        window.confirm("请编辑完成");
      } else {
        this.getFiles(to.query.id);
        this.id = to.query.id;
        next();
      }
    },
    mounted() {
      this.getFiles(this.id);
    },
    methods: {
      getFiles(id) {
        http.get("https://api.github.com/gists/" + id, {
          headers: {
            "Authorization": "token " + localStorage.getItem("token")
          }
        }).then((r) => {
          this.description = r.data.description;
          this.files = Object.values(r.data.files);
          this.files.forEach(function (val) {
            val["oldname"] = val.filename;
          });
          this.activeName = this.files[0].filename;
        })
          .catch();
      },
      onChangeTab(index) {
        this.selected = index;
      },
      onInputChange(index){
        this.$set(this.files[index],"nameChanged",true);
        this.disabledSave = false;
        console.log(index);
      },
      onContentChange(){
        this.disabledSave = false;
      },
      onDisableBtn(){
        this.disabledSave = false;
      },
      addFile(){
        this.files.push({
          filename: this.newFilename,
          content: this.newContent
        });
        this.disabledSave = false;
      },
      onUpdateItem() {
        this.data.description = this.description;
        this.data.files = {};
        var _self = this;
        this.files.forEach(function (item) {
            if(item.oldname !== undefined){
//                更新文件
                if(item.nameChanged !== undefined){
                    _self.data.files[item.oldname] = {
                      filename:item.filename,
                      content:item.content
                    }
                }
//                删除文件
                if(item.deleted !== undefined){
                    _self.data.files[item.oldname] = null;
                }
            } else {
                if(item.nameChanged !== undefined){
//                    添加文件
                  _self.data.files[item.filename] = {
                    content:item.content
                  }
                }
            }
        })
        console.log(this.data);
        http.patch("https://api.github.com/gists/" + this.id, this.data, {
          headers: {
            "Authorization": "token " + localStorage.getItem("token")
          }
        }).then((r) => {
          console.log(r);
          this.deleteItem = [];
          this.files = Object.values(r.data.files);
          this.files.forEach(function (val) {
            val["oldname"] = val.filename;
          })
          bus.$emit("updateList",{
            id:this.id,
            files:r.data.files,
            description:this.description
          });
        })
          .catch();
        this.disabledSave = true;
        this.gistChanged = false;
      }
    }
  }
</script>

<style scoped>
  .tab-tit {
    border-bottom: 2px solid #e4e7ed;
    margin-bottom: 20px;
  }
  .btn{
    padding-left: 80px;
  }
  .title{
    background-color: #f5f5f5;
    padding: 7px 12px;
  }
</style>



