<template>
  <div>
    <ul class="nav">
      <li is="ListItem"
          :item="item"
          :index="index"
          v-for="(item,index) in list"
          :class="{active:current === index}"
          @changeCurrent="onChangeCurrent">
      </li>
    </ul>
    <!--翻页-->
    <div class="pager">
      <el-pagination
        @current-change="handleCurrentChange"
        layout="prev,pager,next"
        :page-size="pageSize"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import http from "axios"
  import ListItem from '@/components/List/ListItem'
  import bus from "./../../bus";

  export default {
    name: "List",
    components:{ListItem},
    data() {
      return {
        list: [],
        tabs: [],
        fileNumber: "",
        current: 0,
        links: {},
        pageSize: 0,
        total: 0,
      }
    },
    mounted() {
      this.pageSize = 15;
      var _self = this;
      this.getCurrentPage("https://api.github.com/gists?page=1&per_page=" + this.pageSize, function () {
        var pageArr = this.links.last.match(/.+page=(\d).+/);
        this.total = this.pageSize * parseInt(pageArr[1]);
      });
      bus.$on('updateList', this.onUpdateList);
    },
    methods: {
      getCurrentPage(url, funct){
        http.get(url, {
          headers: {
            "Authorization": "token " + localStorage.getItem("token")
          }
        }).then((r) => {
          this.list = r.data;
          this.$router.push({ path: '/gists/gist', query: { id: this.list[0].id }});
          var _self = this;
          r.headers.link.split(",").forEach(function (val) {
            var href = val.match(/<(.+)>; rel="(.+)"/);
            _self.$set(_self.links, href[2], href[1]);
          });
          if (typeof funct === "function") {
            funct.call(_self);
          }
        })
          .catch();
      },
      handleCurrentChange(index){
        var strUrl = "https://api.github.com/gists?page=num&per_page=" + this.pageSize;
        this.getCurrentPage(strUrl.replace(/num/, index));
      },
      onChangeCurrent(index){
        this.current = index;
        console.log(index);
      },
      onUpdateList(obj){
        this.list.forEach(function (item) {
          if(item.id === obj.id){
            item.description = obj.description;
            item.files = obj.files;
          }
        });
      }
    }
  }
</script>
<style scoped>
  .nav {
    text-align: left;
    padding-left: 0;
  }
  .nav a {
    color: #333;
  }
  .nav li {
    padding: 5px 20px;
    margin: 5px 0;
  }
  .nav li a{
    color:#333;
  }
  .nav li:hover,
  .nav li.active {
    background-color: #d4d4d4;
  }
  .item-number b {
    color: #666;
  }
  .pager{
    text-align: center;
  }
</style>



