<template>
  <div :class="{hide: item.deleted}">
    <a @click="$emit('changeTab',index)" :class="{active: selected === index}">
      {{item.filename}}
    </a>
    <i class="el-icon-close" @click="deleteFiles"></i>
  </div>
</template>
<script>
  export default {
    name: "TabTit",
    props: ["item","index","selected"],
    data(){
      return {}
    },
    methods:{
      deleteFiles(){
        this.$set(this.item,"deleted",true);
        this.$emit("disabledBtn");
      }
    }
  }
</script>
<style scoped>
  a {
    color: #333;
    padding: 10px 20px;
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
  }
  a:hover{
    color:#409eff;
  }
  .active{
    border-bottom: 2px solid #409eff;
    position: relative;
    bottom:-2px;
  }
  .el-icon-close{
    cursor: pointer;
  }
  .hide{
    display: none;
  }
</style>



