<template>
  <div class="tit"  :class="{hide:hideUserBar}">
    <span>你好,<b>{{userName}}</b>!欢迎来到Gist</span>
    <span class="photoImg"><img :src="photo" /></span>
    <span class="quit"><a @click="userExit">退出</a></span>
  </div>
</template>

<script>
  import client from "./../../js/github-client"
  import http from "axios"
  import bus from "./../../bus";

  export default {
    name: "UserBar",
    data(){
      return {
        userName:"",
        photo:"",
        hideUserBar:true
      }
    },
    mounted(){
      bus.$on("gotAccessToken", this.loadUser);
    },
    methods:{
      userExit(){
        this.hideUserBar = true;
        this.$router.push({path:"/"});
        bus.$emit("removeToken");
      },
      loadUser() {
        client.get("/user")
          .then((r)=>{
            this.hideUserBar = false;
            this.userName = r.login;
            this.photo = r.avatar_url;
          })
          .catch();
      }
    }
  }
</script>

<style scoped>
  .tit {
    width:100%;
    height: 30px;
    line-height: 30px;
    background-color: #f5f5f5;
    text-align: left;
  }
  .tit span.quit{
    float: right;
    margin-right: 20px;
  }
  .tit span{
    display: inline-block;
    padding: 0 10px;
  }
  .photoImg img{
    width:30px;
    height: 30px;
    position: relative;
    top:-2px;
  }
  .tit span,
  .photoImg img{
    vertical-align: middle;
  }
</style>



